import React from "react";

import "./SouthAfricanMap.css";

import saMap from "./world-map.svg";

import "./SouthAfricanMap.css";
export default function SouthAfricanMap() {
  return (
    <div className="SA-Map">
      <h4>South African Map</h4>
      <figure>
        <img src={saMap} alt="south african map" />
      </figure>
      <footer>
        <h4>Total Positive</h4>
        <h3>450</h3>
        <button>View map</button>
      </footer>
    </div>
  );
}
